<!--  -->
<template>
  <div>
    <div class="list-head">
      <router-link to="addOutfit" class="pry-btn list-head_left">
        <el-button type="primary">添加观点</el-button>
      </router-link>
      <div class="list-head_right">
        <el-button type="info" @click="searchFn">查询</el-button>
        <el-date-picker v-model="endDate" type="date" placeholder="结束时间"></el-date-picker>
        <el-date-picker v-model="startDate" type="date" placeholder="开始时间"></el-date-picker>
      </div>
    </div>
    <el-table :data="tableData">
      <el-table-column prop="date" label="观点日期"></el-table-column>
      <el-table-column prop="view_content" label="内容"></el-table-column>
      <el-table-column prop="institution_name" label="机构名称"></el-table-column>
      <el-table-column prop="institution_type" label="市场情绪"></el-table-column>
      <el-table-column label="操作" width="230px">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      typeList: ["看空", "震荡", "看多"],
      startDate: "",
      endDate: "",
      options: [
        {
          value: "选项1",
          label: "黄金糕"
        },
        {
          value: "选项2",
          label: "双皮奶"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        },
        {
          value: "选项4",
          label: "龙须面"
        },
        {
          value: "选项5",
          label: "北京烤鸭"
        }
      ],
      value4: ""
    };
  },

  methods: {
    handleEdit(row) {
      console.log(row);
      this.$router.push({name:'添加机构观点',
        params:row
      })
    },
    format(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    searchFn() {
      let params = {
        start: this.format(this.startDate),
        end: this.format(this.endDate)
      };
      this.$x.post(this.url.insList, params).then(res => {
        this.tableData = res.data;
        this.tableData.map((ele, i) => {
          this.tableData[i].institution_type = this.typeList[
            ele.institution_type
          ];
        });
      });
    }
  },
  mounted() {
    var nowDate = new Date();
    let params = {
      start: this.format(nowDate),
      end: this.format(nowDate)
    };
    console.log(params);
    this.$x.post(this.url.insList, params).then(res => {
      this.tableData = res.data;
    });
  }
};
</script>
<style>
.pry-btn {
  color: #fff;
}
.list-head {
  display: flex;
}
.list-head_left {
  float: left;
  width: 16%;
}
.list-head_right {
  width: 84%;
  float: right;
  /* display:flex; */
  /* justify-content: space-around; */
}
.list-head_right .el-input,
.list-head_right .el-select,
.list-head_right .el-button {
  width: 120px;
  float: right;
  margin-left: 10px;
}
.list-head_right .el-button {
  width: 80px;
}
</style>

